// ui.js
var Ui = pc.createScript('ui');

Ui.attributes.add('css', {type: 'asset', assetType:'css', title: 'CSS Asset'});
Ui.attributes.add('html', {type: 'asset', assetType:'html', title: 'HTML Asset'});


Ui.prototype.initialize = function () {
    // create STYLE element
    var style = document.createElement('style');

    // append to head
    document.head.appendChild(style);
    style.innerHTML = this.css.resource || '';
    
    // Add the HTML
    this.div = document.createElement('div');
    this.div.classList.add('container');
    this.div.innerHTML = this.html.resource || '';
    
    
    
     var asset = pc.app.assets.find("left-arrow.png", "texture");
    
    
    const isEditor = document.URL.indexOf("playcanvas") != -1;
    
    
    const url = isEditor? `api/${asset.file.url}` : asset.file.url;
    
    
    //console.log(url);
    
    const prevButton=this.div.querySelector('.optional-prev-arrow');
    prevButton.style.backgroundImage = `url(${url})`;
    
    
    const nextButton=this.div.querySelector('.optional-next-arrow');
    nextButton.style.backgroundImage = `url(${url})`;
    
    if(!IS_MOBILE){
        prevButton.style.display="NONE";
        nextButton.style.display="NONE";
    }
    
    // append to body
    // can be appended somewhere else
    // it is recommended to have some container element
    // to prevent iOS problems of overfloating elements off the screen
    document.body.appendChild(this.div);
    
    
    this.counter = 0;
    
    this.bindEvents();
};

