// model-controller.js
var ModelController = pc.createScript('modelController');


ModelController.attributes.add('optionalEntities', {
    type: 'entity',
    description: 'Optionals',
    array: true
});

let currentIndex = 0;

// initialize code called once per entity
ModelController.prototype.initialize = function() {
    for(let optional of this.optionalEntities){
        optional.enabled = false;
    }
    
    this.optionalEntities[currentIndex].enabled = true;
    
    this.app.on('OptionalSelected', (selected)=>{
        
        const index = parseInt(selected.split("_")[1]);
        
        this.optionalEntities[currentIndex].enabled = false;
        this.optionalEntities[index].enabled = true;
        currentIndex = index;
    });
    
    this.app.on('SelectNextOptional', SelectNextOptional.bind(this));
    
    this.app.on('SelectPrevOptional', SelectPrevOptional.bind(this));
};


// update code called every frame
ModelController.prototype.update = function(dt) {
   if (this.app.keyboard.wasPressed(pc.KEY_LEFT)) {
       SelectPrevOptional.bind(this)();
   } else if (this.app.keyboard.wasPressed(pc.KEY_RIGHT)) {
       SelectNextOptional.bind(this)();
   }
};



function SelectNextOptional(){
    this.optionalEntities[currentIndex].enabled = false;
    const newIndex = (currentIndex+1) % this.optionalEntities.length;
    this.optionalEntities[newIndex].enabled = true;
    currentIndex = newIndex;
    this.app.fire("OnSelectedChange",currentIndex);
    //TriggerSelectedChange();
}

function SelectPrevOptional(){
    this.optionalEntities[currentIndex].enabled = false;
    const newIndex = currentIndex-1 < 0 ? this.optionalEntities.length - 1 : currentIndex-1;
    this.optionalEntities[newIndex].enabled = true;
    currentIndex = newIndex;
    pc.app.fire("OnSelectedChange",currentIndex);
    //TriggerSelectedChange();
}

function TriggerSelectedChange(){
    this.app.fire("OnSelectedChange",currentIndex);
}


